import React, { useState, useEffect } from 'react';
import Navbar from '../navbar/Navbar';
import {
  PieChart, Pie, Tooltip,
  ResponsiveContainer,
  LineChart, Line, XAxis, YAxis, CartesianGrid, Legend
} from 'recharts';
import { motion, AnimatePresence } from 'framer-motion';

import "./Awsdashboard.css";

// Dancing dots loader component
const DancingDotsLoader = () => {
  return (
    <div style={{ display: 'flex', gap: 4, alignItems: 'flex-end', height: 20 }}>
      {[0, 1, 2].map(i => (
        <span
          key={i}
          style={{
            width: 6,
            height: 6,
            borderRadius: '50%',
            backgroundColor: '#E43737',
            display: 'inline-block',
            animation: `dance 1.2s ease-in-out ${i * 0.2}s infinite`,
          }}
        />
      ))}
      <style>{`
        @keyframes dance {
          0%, 100% { transform: translateY(0); }
          50% { transform: translateY(-8px); }
        }
      `}</style>
    </div>
  );
};

function Awsdashboard() {
  const [isConfigOpen, setIsConfigOpen] = useState(false);
  const toggleConfig = () => setIsConfigOpen(prev => !prev);

  // State for AWS regions
  const [regions, setRegions] = useState([]);
  const [selectedRegion, setSelectedRegion] = useState('');

  // Loading states
  const [loadingRegions, setLoadingRegions] = useState(true);
  const [loadingInstanceStatus, setLoadingInstanceStatus] = useState(true);
  const [loadingInstanceTypes, setLoadingInstanceTypes] = useState(true);

  // Instance status state
  const [instanceStatus, setInstanceStatus] = useState({
    total: 0,
    running: 0,
    stopped: 0,
  });

  // Instance types data for line chart
  const [instanceTypesData, setInstanceTypesData] = useState([]);

  const [instanceDetailsData, setInstanceDetailsData] = useState([]);
  const [loadingInstanceDetails, setLoadingInstanceDetails] = useState(false);

  // Fetch AWS regions on mount
  useEffect(() => {
    const fetchAllData = async () => {
      try {
        setLoadingRegions(true);
        setLoadingInstanceStatus(true);
        setLoadingInstanceTypes(true);
        setLoadingInstanceDetails(true);
  
        const [
          regionsRes,
          statusRes,
          detailsRes,
          typesRes
        ] = await Promise.all([
          fetch('http://127.0.0.1:8000/api/aws/regions').then(res => res.json()),
          fetch('http://127.0.0.1:8000/api/aws/ec2-instance-status').then(res => res.json()),
          fetch('http://127.0.0.1:8000/api/aws/ec2-instance-details').then(res => res.json()),
          fetch('http://127.0.0.1:8000/api/aws/ec2-instance-types').then(res => res.json()),
        ]);
  
        // Handle regions
        const regionList = regionsRes.regions || [];
        setRegions(regionList);
        if (regionList.length > 0) {
          setSelectedRegion(regionList[0]);
        }
  
        // Handle instance status
        setInstanceStatus({
          total: statusRes.total || 0,
          running: statusRes.running || 0,
          stopped: statusRes.stopped || 0,
        });
  
        // Handle instance details
        setInstanceDetailsData(detailsRes.instances || []);
  
        // Handle instance types
        const chartData = Object.entries(typesRes).map(([name, instances]) => ({
          name,
          instances,
        }));
        setInstanceTypesData(chartData);
  
      } catch (error) {
        console.error("Failed to fetch AWS dashboard data:", error);
      } finally {
        setLoadingRegions(false);
        setLoadingInstanceStatus(false);
        setLoadingInstanceTypes(false);
        setLoadingInstanceDetails(false);
      }
    };
  
    fetchAllData();
  }, []);

  const handleStart = async (instance_id) => {
    try {
      const res = await fetch("http://127.0.0.1:8000/api/start-instance", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ instance_id }),
      });
      const data = await res.json();
      console.log(data);
      // Refresh instances or update state accordingly
    } catch (error) {
      console.error("Failed to start instance:", error);
    }
  };

  const handleStop = async (instance_id) => {
    try {
      const res = await fetch("http://127.0.0.1:8000/api/stop-instance", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ instance_id }),
      });
      const data = await res.json();
      console.log(data);
      // Refresh instances or update state accordingly
    } catch (error) {
      console.error("Failed to stop instance:", error);
    }
  };

  const [accessKey, setAccessKey] = useState('');
  const [secretKey, setSecretKey] = useState('');
  const [regionInput, setRegionInput] = useState('');
  
  const [loadingCreds, setLoadingCreds] = useState(false);
  const [savedCreds, setSavedCreds] = useState(null);

  useEffect(() => {
    const fetchCredentials = async () => {
      try {
        const res = await fetch('http://127.0.0.1:8000/api/aws/get-credentials');
        if (!res.ok) throw new Error("Failed to fetch credentials");
        const data = await res.json();
  
        setAccessKey(data.access_key || '');
        setSecretKey(data.secret_key || '');
        setRegionInput(data.region || '');
        setSavedCreds(data);
      } catch (err) {
        console.error('Error fetching credentials:', err);
        setAccessKey('');
        setSecretKey('');
        setRegionInput('');
        setSavedCreds(null);
      }
    };
  
    if (isConfigOpen) {
      fetchCredentials();
    }
  }, [isConfigOpen]);

  const handleConfigure = async () => {
    if (!accessKey || !secretKey || !regionInput) {
      alert("Please fill in all fields.");
      return;
    }
  
    setLoadingCreds(true);
  
    try {
      // Step 1: POST credentials
      const postRes = await fetch('http://127.0.0.1:8000/api/aws/validate-and-store', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          access_key: accessKey,
          secret_key: secretKey,
          region: regionInput
        })
      });
  
      if (!postRes.ok) throw new Error('Failed to save credentials.');
  
      // Step 2: GET saved credentials
      const getRes = await fetch('http://127.0.0.1:8000/api/aws/get-credentials');
      const data = await getRes.json();
  
      setSavedCreds(data);
      alert("Credentials saved successfully.");
    } catch (error) {
      console.error("Error configuring credentials:", error);
      alert("Failed to configure credentials.");
    } finally {
      setLoadingCreds(false);
    }
  };

  return (
    <div>
      <div><Navbar /></div>

      {/* Config Panel */}
      <AnimatePresence>
  {isConfigOpen && (
    <motion.div
      className="config-panel"
      initial={{ x: 400 }}
      animate={{ x: 0 }}
      exit={{ x: 400 }}
      transition={{ duration: 0.4, ease: "easeOut" }}
    >
      <div className="config-header">
        <div>Configuration</div>
        <button className="close-config" onClick={toggleConfig}>✕</button>
      </div>

      <div className="config-body">
        <div>
          To access and display your AWS cloud data in this dashboard, please provide your AWS credentials...
        </div>

        <div>
          <div className='input-prime'>
            <input
              placeholder='AWS Access Key'
              value={accessKey}
              onChange={(e) => setAccessKey(e.target.value)}
            />
          </div>
          <div className='input-prime'>
            <input
              placeholder='AWS Secret Key'
              value={secretKey}
              onChange={(e) => setSecretKey(e.target.value)}
            />
          </div>
          <div className='input-prime'>
            <input
              placeholder='Region'
              value={regionInput}
              onChange={(e) => setRegionInput(e.target.value)}
            />
          </div>
        </div>

        <div className='prime-button'>
          <button onClick={handleConfigure} disabled={loadingCreds}>
            <div className='icons btn-icon'>
              <i className="bi bi-floppy2"></i>
            </div>
            <div>{loadingCreds ? 'Saving...' : 'Configure/Save'}</div>
          </button>
        </div>
      </div>
    </motion.div>
  )}
</AnimatePresence>

      {/* Main Content */}
      <motion.div
        className='main'
        initial={{ opacity: 0, y: 50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6, ease: "easeOut" }}
      >
        <div>
          <div className='top-bar'>
            <div className='menu-items topbar'>
              <ul>
                <li>
                  <div><i className="bi bi-cloud icons"></i></div>
                  <div className='menu-text topbar-text'>Cloud dashboard</div>
                </li>
              </ul>
            </div>
          </div>

          <div className='menu-options'>
            <div className='options_'>
              <div className='options'>
                {loadingRegions ? (
                  <DancingDotsLoader />
                ) : (
                  <select
                    value={selectedRegion}
                    onChange={(e) => setSelectedRegion(e.target.value)}
                  >
                    {regions.map(region => (
                      <option key={region} value={region}>{region}</option>
                    ))}
                  </select>
                )}
              </div>
              <div className='account margin-left'>
                <span className='label'>Account:</span> Production
              </div>
            </div>
            <div className='all-btns'>
              <div className='prime-button'>
                <button onClick={() => window.location.reload()}>
                  <div className='icons btn-icon'><i className="bi bi-arrow-clockwise"></i></div>
                  <div>Refresh</div>
                </button>
              </div>
              <div className='secondary-button margin-left'>
                <button onClick={toggleConfig}>
                  <div className='icons btn-icon'><i className="bi bi-sliders"></i></div>
                  <div>Configure</div>
                </button>
              </div>
            </div>
          </div>

          <div className='analysis'>
            <div className='instance-status'>
              <div className='instance-main-container'>
                <div className='instance-container'>
                  <div>
                    <div className='label'>Total</div>
                    <div className='count'>
                      {loadingInstanceStatus ? <DancingDotsLoader /> : instanceStatus.total}
                    </div>
                  </div>
                  <div><i className="bi bi-tv-fill icons"></i></div>
                </div>
              </div>
              <div className='instance-main-container'>
                <div className='instance-container'>
                  <div>
                    <div className='label'>Running</div>
                    <div className='count'>
                      {loadingInstanceStatus ? <DancingDotsLoader /> : instanceStatus.running}
                    </div>
                  </div>
                  <div><i className="bi bi-heptagon-half icons"></i></div>
                </div>
              </div>
              <div className='instance-main-container'>
                <div className='instance-container'>
                  <div>
                    <div className='label'>Stopped</div>
                    <div className='count'>
                      {loadingInstanceStatus ? <DancingDotsLoader /> : instanceStatus.stopped}
                    </div>
                  </div>
                  <div><i className="bi bi-slash-circle icons"></i></div>
                </div>
              </div>
            </div>

            <div className='status-dis'>
              <div className='chart-title'>Instance Status</div>
              {loadingInstanceStatus ? (
                <DancingDotsLoader />
              ) : (
                <ResponsiveContainer width="100%" height="100%">
                  <PieChart>
                    <Pie
                      dataKey="value"
                      data={[
                        { name: 'Running', value: instanceStatus.running },
                        { name: 'Stopped', value: instanceStatus.stopped },
                      ]}
                      cx="50%"
                      cy="50%"
                      innerRadius={50}
                      outerRadius={80}
                      fill="#E43737"
                      label
                    />
                    <Tooltip />
                  </PieChart>
                </ResponsiveContainer>
              )}
            </div>

            <div className='instance-types'>
              <div className='chart-title'>Instance Types</div>
              {loadingInstanceTypes ? (
                <DancingDotsLoader />
              ) : (
                <ResponsiveContainer width="100%" height="100%">
                  <LineChart
                    data={instanceTypesData}
                    margin={{ top: 70, right: 30, left: 20, bottom: 5 }}
                  >
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="name" />
                    <YAxis />
                    <Tooltip />
                    <Legend />
                    <Line
                      type="monotone"
                      dataKey="instances"
                      stroke="#E43737"
                      strokeWidth={3}
                      dot={{ fill: '#E43737', r: 2 }}
                    />
                  </LineChart>
                </ResponsiveContainer>
              )}
            </div>
          </div>

          <div className='instances'>
            <div className='instances-container'>
              <div className='instances-title-container'>Instances</div>
              <div className='table-headings'>
                <div className='head1'>Instance name</div>
                <div className='head2'>Status</div>
                <div className='head3'>Instance id</div>
                <div className='head4'>Public ip</div>
                <div className='head5'>Instance type</div>
                <div className='head6'>Launch date</div>
                <div className='head7'>Actions</div>
              </div>

              {instanceDetailsData && instanceDetailsData.length > 0 ? (
                instanceDetailsData.map((instance) => (
                  <div className='table-data' key={instance.instance_id}>
                    <div className='head1'>{instance.name}</div>
                    <div className={instance.status === 'stopped' ? 'state-stop head2' : 'state head2'}>
                      {instance.status}
                    </div>
                    <div className='head3'>{instance.instance_id}</div>
                    <div className='head4'>{instance.public_ip}</div>
                    <div className='head5'>{instance.instance_type}</div>
                    <div className='head6'>{instance.launch_date}</div>
                    <div className='head7'>
                      {/* Play icon to start */}
                      <span
                        className={`margin-right ${instance.status === 'running' ? 'disabled' : 'clickable'}`}
                        onClick={() => instance.status !== 'running' && handleStart(instance.instance_id)}
                        title={instance.status === 'running' ? 'Instance already running' : 'Start instance'}
                        style={{ cursor: instance.status === 'running' ? 'not-allowed' : 'pointer', opacity: instance.status === 'running' ? 0.5 : 1 }}
                      >
                        <i className="bi bi-play-fill"></i>
                      </span>

                      {/* Stop icon */}
                      <span
                        className={`${instance.status === 'stopped' ? 'disabled' : 'clickable'}`}
                        onClick={() => instance.status !== 'stopped' && handleStop(instance.instance_id)}
                        title={instance.status === 'stopped' ? 'Instance already stopped' : 'Stop instance'}
                        style={{ cursor: instance.status === 'stopped' ? 'not-allowed' : 'pointer', opacity: instance.status === 'stopped' ? 0.5 : 1 }}
                      >
                        <i className="bi bi-slash-circle-fill"></i>
                      </span>
                    </div>
                  </div>
                ))
              ) : (
                <div>No instances found.</div>
              )}
            </div>
          </div>
        </div>
      </motion.div>
    </div>
  );
}

export default Awsdashboard;
